export default {
  commonFunc: {
    /*
         * elementUTC
         * return  */
    elementDateFormat(time) {
      let formatTime = new Date(time)
      return new Date(formatTime.setHours(formatTime.getHours() + 8))
    }
  },
  /*
     * 
     * return  */
  conmmonMethods: {
    /*
         * 
         * return  */
    getNowFormatDate() {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate
      return currentdate
    }
  },
  /*
     * 
     * return  */
  make_clear(data) {
    if (data) {
      for (let item in data) {
        data[item] = ''
      }
    }
  },
  /* 
     * 
     * return  */
  getNewArray(array, a) {
    if (Array.isArray(array)) {
      let newArray = array.map(function(item) {
        return item[a]
      })
      return newArray
    }
  },
  /* 
     * element - uiMessageBox 
     * return  */
  makePopBox(words) {
    // this.makePopBox('').then(() => {})
    let _this = this
    return new Promise(function(resolve) {
      _this
        .$confirm('' + words, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        })
        .then(() => {
          resolve()
        })
        .catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          })
        })
    })
  },
  /* 
     * 
     * return  */
  makeDate({ startTs, endTs }) {
    var dateOne = new Date(startTs)
    var dateTwo = new Date(endTs)
    let ck = dateTwo.getTime() - dateOne.getTime()
    return ck
  },
  // 
  dateFtt(fmt, date) {
    const o = {
      'M+': date.getMonth() + 1, // 
      'd+': date.getDate(), // 
      'h+': date.getHours(), // 
      'm+': date.getMinutes(), // 
      's+': date.getSeconds(), // 
      'q+': Math.floor((date.getMonth() + 3) / 3), // 
      S: date.getMilliseconds() // 
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
      )
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        )
      }
    }
    return fmt
  }
}
